:root{
  --purple-pastel: rgb(195, 177, 225);
}

@import url("http://fonts.cdnfonts.com/css/roboto");

* {
  font-family: "Roboto", sans-serif;
}

.title-header {
  font-family: "Roboto Light", sans-serif;
  color: rgb(153, 106, 194);
}

.btn-cpurple {
  background-color: var(--purple-pastel);
  color: rgb(0, 0, 0);
  padding: 0.5em;
}

.btn-cpurple:hover {
  background-color: rgb(147, 88, 199);
  color: rgb(255, 255, 255);
}

.bottom-title{
    color: rgb(0, 0, 0);
}

.place-footer{
  background-color: white;
  position: -webkit-sticky;
}



